.files{
	/*margin-bottom: -2rem;
	margin-top: 3rem;*/
	padding-top: 1rem;
	.clearfix;
	&._two-cols{
		.file{
			width: 57rem;
			&:nth-child(3n){
				margin-right: 3rem;
			}
			&:nth-child(2n){
				margin-right: 0;
			}
		}
	}
}
.file{
	.color(@title-color);
	border: 1px solid @cBorder;
	border-radius: 2/@rem;
	position: relative;
	font-size: 12/@rem;
	letter-spacing: 0.04/@rem;
	line-height: 20/@rem;
	padding: 1.4rem 3rem 1.4rem 6.9rem;
	background-color: #fff;
	width: 370/@rem;
	float: left;
	margin-right: 3rem;
	margin-top: 2rem;
	&:nth-child(3n){
		margin-right: 0;
	}

	&:before{
		content: '';
		position: absolute;
		left: 19/@rem;
		top:19/@rem;
		width: 25/@rem;
		height: 29/@rem;
		.background('@{pathToImg}icons/file.svg');
	}
	&__inner{

	}
	&__name{
		white-space: nowrap;
		overflow:hidden;
		text-overflow: ellipsis;
	}
	&__props{
		font-weight: 600;
		color: @cGold;
	}
}
.tablet.portrait{
	.files{
		//margin-top: 2rem;
		padding-top: 0;
	}
	.file{
		width: 342/@rem;
		&:nth-child(3n){
			margin-right: 3rem;
		}
		&:nth-child(2n){
			margin-right: 0;
		}
	}
}
.mobile{
	.files{
		padding-top: 0;
		//margin-top: 2rem;
		&._two-cols{
			.file{
				width: 100%;
			}
		}
	}
	.file{
		width: 100%;
		margin-right: 0;
		&__name{
			white-space: normal;
			overflow: visible;
		}
	}
}